<template>
	<div @click="changeStage()">
		<el-container :style="{ backgroundColor: bgColor }">
			<el-main id="startPage" v-show="isShow('START')">
				<i class="el-icon-switch-button"></i>
				点击屏幕开始
			</el-main>
			<el-main id="loadingPage" v-show="isShow('LOADING')">
				<i class="el-icon-loading"></i>
				请等待页面变绿后点击屏幕
			</el-main>
			<el-main id="waitingPage" v-show="isShow('WAITING')">
				<i class="el-icon-bottom"></i>
				速点！
			</el-main>
			<el-main id="endPage" v-show="isShow('END')">
				<i class="el-icon-s-promotion"></i>
				<div>反应时间</div>
				<div>{{ reactionTime }}ms</div>
				<div id="restart">
					<i class="el-icon-refresh-right"></i>
					再次点击屏幕以重新开始
				</div>
			</el-main>
			<el-main id="wrongPage" v-show="isShow('WRONG')">
				<i class="el-icon-warning"></i>
				点得太早了
				<div id="restart">
					<i class="el-icon-refresh-right"></i>
					再次点击屏幕以重新开始
				</div>
			</el-main>
		</el-container>
	</div>
</template>

<script>
export default {
	data() {
		return {
			isStart: true,
			status: {
				START: "#2b87d1",
				LOADING: "#ce2636",
				WAITING: "#4bdb6a",
				END: "#2b87d1",
				WRONG: "#2b87d1",
			},
			stage: 0,
			delay: 0,
			timer: null,
			startTime: null,
			reactionTime: 0,
			clickAble: true,
		};
	},
	computed: {
		bgColor() {
			return this.status[this.statusNow];
		},
		statusNow() {
			return this.statusKeys[this.stage];
		},
		statusKeys() {
			return Object.keys(this.status);
		},
	},
	watch: {
		statusNow: function () {
			if (this.statusNow == "LOADING") {
				this.delay = Math.random() * 2000 + 1500;
				this.startTime = new Date();
				this.clickAble = false;
				this.timer = setTimeout(() => {
					this.clickAble = true;
					this.changeStage();
					this.startTime = new Date();
				}, this.delay);
			}
			if (this.statusNow == "END") {
				clearTimeout(this.timer);
				this.reactionTime = new Date() - this.startTime;
			}
		},
	},
	props: {},
	methods: {
		changeStage() {
			if (!this.clickAble) {
				this.stage = this.statusKeys.length - 1;
			} else
				this.stage =
					this.stage >= this.statusKeys.length - 2 ? 1 : this.stage + 1;
		},
		isShow(status) {
			return status == this.statusNow;
		},
	},
};
</script>
<style lang="scss">
.el-container {
	position: absolute;
	height: 100%;
	width: 100%;
	cursor: pointer;
	.el-main {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		color: white;
		font-size: 25px;
		user-select: none;
		i {
			font-size: 100px;
			color: white;
			margin-bottom: 20px;
		}
	}
	#restart {
		position: relative;
		font-size: 16px;
		top: 100px;
		bottom: 100px;
		i {
			font-size: 16px;
		}
	}
}
</style>
